.half-card-slider {
  width: 50%;
}

@media (max-width: 1024px) {
  .half-card-slider {
    width: 66.66666%;
  }
  .half-card-slider--inner {
    width: calc(50% - 16px);
  }
}
@media (max-width: 767px) {
  .half-card-slider {
    width: 100%;
  }
}/*# sourceMappingURL=half-card-slider.css.map */
